import Song from '@/components/Song.vue'
export default {
  components: {
    Song
  },
  data () {
    return {
      recommendList: []
    }
  },
  mounted () {
    this.$http({
      method: 'GET',
      url: './static/data/songlist.json'
    })
    .then(response => {
      this.recommendList = response.data
    })
  },
  methods: {
    updateTitle (name) {
      this.$store.dispatch('updateTitle', name)
    }
  }
}



